/**
 * RegExp
 * @param {String} regexStrings 
 * @returns 20RegExp1
 */
export function getRegExpFrom(regexStrings) {
    const restrArray = regexStrings.split('\n');
    const errors = [];
    const regexes = [];
    for (const restr of restrArray) {
        if (restr === '') {
            //
            continue;
        }
        try {
            const re = new RegExp(restr, 'mg');
            regexes.push(re);
        } catch (error) {
            errors.push(error);
        }
    }
    return [regexes, errors];
}

/**
 * RegExp
 * @param {RegExp[]} regExpArray RegExp
 * @returns 
 */
export function getRegexStringsFrom(regExpArray) {
    const regexes = [];
    for (let i = 0; i < regExpArray.length; i++) {
        regexes.push(regExpArray[i].source);
    }
    return regexes;
}

const utils = {
    getRegExpFrom,
    getRegexStringsFrom,
};

export default utils;
